/* Colors */
$color-white: #fff;
$color-light-blue: #AAEEFF;
$color-yellow: #FDE995;
$color-nav-top: #373A3C;

/* Margin & Padding */
$padding-sections: 70px;

/* CSS */
section {
  margin: 0;
  padding: $padding-sections 0;
}

/* Backgrounds */
section.bkg-white {
  background-color: $color-white;
}

section.bkg-blue {
  background-color: $color-light-blue;
}

section.bkg-yellow {
  background-color: $color-yellow;
}
.reviews article {
  background-color: $color-white;
}